.logo__icon {
    width: 96px;
    height: 43px;

    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        fill: $hover;
    }

    @media screen and (min-width: $pc) {
        width: 158px;
        height: 71px;
    }
}

@media screen and (max-width: 767.98px) {
    .logo--position {
        margin-left: auto;
        margin-right: auto;
    }
}

.logo__icon--mobile {
    width: 190px;
    height: 85px;
}